<template>
    <div>
        <h2>Список поточних фільмів</h2>
        <div class="films-main">
            <div class="film-main" v-for="film in films" :key="film.id" @click="filmClick(film.id)">
                <img :src="film.image" alt="Фото фільму">
                <span>{{ film.title }}</span>
            </div>
        </div>
    </div>
    <div class="mt">
        <h2>Список фільмів, які скоро вийдуть</h2>
        <div class="films-main">
            <div class="film-main" v-for="film in films2" :key="film.id" @click="filmClick(film.id)">
                <img :src="film.image" alt="Фото фільму">
                <span>{{ film.title }}</span>
            </div>
        </div>
    </div>
</template>


<script>    

import { ref, onMounted } from 'vue';
import { getDatabase, ref as dbRef, onValue } from 'firebase/database';

export default {

    

    setup() {
    const db = getDatabase()
    const moviesRef = dbRef(db, 'films');
    const films = ref([]);
    const films2 = ref([]);

    onMounted(() => {
      onValue(moviesRef, (snapshot) => {
        const moviesData = snapshot.val();
        const moviesArray = [];
        const moviesArray2 = [];

        for (const key in moviesData) {
          if(moviesData[key].radioData === 'Вже вийшов'){
            moviesArray.push({
              id: moviesData[key].id,
              title: moviesData[key].nameFilm,
              image: moviesData[key].imageFirst,
            });
          } else{
            moviesArray2.push({
              id: moviesData[key].id,
              title: moviesData[key].nameFilm,
              image: moviesData[key].imageFirst,
            });
          }
          
        }

        films.value = moviesArray;
        films2.value = moviesArray2;
      });
    });
    return { films, films2 };
  },


    data(){
        return{

        }
    },

    methods: {
      filmClick(id) {
        this.$router.push({ name: "film", params: { id } });
      },
    },

}
</script>


<style lang="scss">

.films-main{
    display: flex;
    gap: 40px;
    width: 92%;
    margin: 30px auto 0;
}
.film-main{
    display: flex;
    flex-direction: column;
    gap: 10px;
    cursor: pointer;

    img{
        width: 250px;
        height: 250px;
    }

    span{
        font-weight: 600;
        font-size: 20px;
        color: #515558;
    }
}
.mt{
  margin-top: 40px;
}

    
</style>